import type { ApiError } from '@riptech/utils/src/errors/ErrorResponse';
import { ErrorResponse } from '@riptech/utils/src/errors/ErrorResponse';
import type { SellerCentralMarketplace } from '@seller-utilities/database';
import type { SalesAndTrafficReportResponse } from '@seller-utilities/types';

import type { FetchUserDataResponse } from '../pages/api/account/fetch-user-data';
import type { AmazonAppConfigUserInfo } from './AdvChartUtils';
import type { AmazonDateRange } from './request/InlinePageRequest/AdvRequest';

export type RequestProxyType = 'fetchUserData' | 'fetchSalesAndTrafficReport' | 'invalidateUserDataCache' | 'getData';

export interface RequestProxyMessage {
  type: RequestProxyType;
}

type ProxyPayload<T> =
  | {
      type: 'error';
      payload: {
        message: string;
        statusCode: ApiError;
      };
    }
  | { type: 'success'; payload: T };

export interface FetchUserDataMessage extends RequestProxyMessage {
  url: string;
}
export interface FetchSalesAndTrafficReportMessage extends RequestProxyMessage {
  amazonDateRange: AmazonDateRange;
  amazonAppConfigUserInfo?: AmazonAppConfigUserInfo;
  marketplace: SellerCentralMarketplace;
}

export interface InvalidateUserDataCacheMessage extends RequestProxyMessage {
  url: string;
}

export const FETCH_USER_DATA_QUERY_KEY = 'fetchUserData';

export default class RequestProxy {
  static readonly USER_DATA_URL = `${process.env.NEXT_PUBLIC_CLIENT_URL}/api/account/fetch-user-data`;

  static handleError<T>(value: Promise<ProxyPayload<T>>) {
    return value.then((data: ProxyPayload<T>) => {
      if (data.type === 'error') {
        throw new ErrorResponse(data.payload.statusCode, {
          message: data.payload.message
        });
      }
      return data.payload;
    });
  }

  static fetchUserData = () =>
    RequestProxy.handleError<FetchUserDataResponse>(
      chrome.runtime.sendMessage<FetchUserDataMessage, ProxyPayload<FetchUserDataResponse>>(
        process.env.NEXT_PUBLIC_EXTENSION_ID,
        {
          type: 'fetchUserData',
          url: RequestProxy.USER_DATA_URL
        }
      )
    );

  static fetchSalesAndTrafficReport = (
    marketplace: SellerCentralMarketplace,
    amazonDateRange: AmazonDateRange,
    amazonAppConfigUserInfo?: AmazonAppConfigUserInfo
  ) =>
    RequestProxy.handleError<SalesAndTrafficReportResponse>(
      chrome.runtime.sendMessage<FetchSalesAndTrafficReportMessage, ProxyPayload<SalesAndTrafficReportResponse>>(
        process.env.NEXT_PUBLIC_EXTENSION_ID,
        {
          amazonAppConfigUserInfo,
          amazonDateRange,
          marketplace,
          type: 'fetchSalesAndTrafficReport'
        }
      )
    );

  static sendInvalidateUserDataCache = async (queryKey: string): Promise<void> => {
    let url: string | undefined;
    switch (queryKey) {
      case FETCH_USER_DATA_QUERY_KEY: {
        url = RequestProxy.USER_DATA_URL;
        break;
      }
      default:
        break;
    }
    if (url) {
      await chrome.runtime.sendMessage<InvalidateUserDataCacheMessage, undefined>(
        process.env.NEXT_PUBLIC_EXTENSION_ID,
        {
          type: 'invalidateUserDataCache',
          url
        }
      );
    }
  };
}
