import { LoadingButton } from '@mui/lab';
import { CardContent, FormControlLabel, Grid, ListItemText, Stack, Switch, Typography } from '@mui/material';
import { enqueueSnackbar } from '@riptech/minimal-ui/src/components/snackbar';
import type { UIEnhancementSettings } from '@seller-utilities/database';
import { useMutation } from '@tanstack/react-query';
import { useFormik } from 'formik';
import type { FC } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import useInvalidateCachedData from '../../hooks/useInvalidateCachedData';
import UserActions from '../../redux/actions/userActions';
import UserSelectors from '../../redux/selectors/userSelectors';
import ServerlessAPI from '../../utils/api/serverless/ServerlessAPI';
import { FETCH_USER_DATA_QUERY_KEY } from '../../utils/RequestProxy';
const FORM_INPUTS: Array<{
  header: string;
  items: Array<{
    name: keyof UIEnhancementSettings;
    label: string;
  }>;
}> = [{
  header: 'Amazon Product Details Page',
  items: [{
    name: 'asinList',
    label: 'Child ASIN Table'
  }, {
    name: 'productDetailsReviews',
    label: 'Reviews and Ratings'
  }]
}, {
  header: 'Amazon Search Results Pages',
  items: [{
    name: 'searchResultsReviews',
    label: 'Reviews and Ratings'
  }]
}, {
  header: 'Amazon Advertising Console',
  items: [{
    name: 'advChart',
    label: 'Campaigns Page Chart'
  }]
}];
const UIEnhancementsForm: FC = () => {
  const dispatch = useDispatch();
  const uiEnhancementSettings = useSelector(UserSelectors.uiEnhancementSettingsSelector);
  const invalidateUserSettings = useInvalidateCachedData(FETCH_USER_DATA_QUERY_KEY);
  const updateUIEnhancementSettings = useMutation({
    meta: {
      errorMessage: `An error occurred while updating settings`
    },
    mutationFn: (values: UIEnhancementSettings) => ServerlessAPI.updateUIEnhancementSettings({
      uiEnhancements: values
    }),
    onSuccess: async () => {
      enqueueSnackbar('Successfully updated UI enhancement settings', {
        variant: 'success'
      });
    }
  });
  const formik = useFormik<UIEnhancementSettings>({
    initialValues: {
      advChart: !!uiEnhancementSettings?.advChart,
      asinList: !!uiEnhancementSettings?.asinList,
      searchResultsReviews: !!uiEnhancementSettings?.searchResultsReviews,
      productDetailsReviews: !!uiEnhancementSettings?.productDetailsReviews
    },
    onSubmit: async (values, formikHelpers) => updateUIEnhancementSettings.mutate(values, {
      onError: () => {
        formikHelpers.setSubmitting(false);
      },
      onSuccess: () => {
        invalidateUserSettings();
        dispatch(UserActions.setUIEnhancementSettings(values));
        formikHelpers.setSubmitting(false);
      }
    })
  });
  return <CardContent sx={{
    p: 0,
    gap: 3,
    display: 'flex',
    flexDirection: 'column'
  }} data-sentry-element="CardContent" data-sentry-component="UIEnhancementsForm" data-sentry-source-file="UIEnhancementsForm.tsx">
      {FORM_INPUTS.map(section => <Grid key={section.header} container gap={3} justifyContent="space-between">
          <Grid md={4} xs={12}>
            <ListItemText primary={section.header} primaryTypographyProps={{
          typography: 'h6',
          mb: 0.5
        }} secondaryTypographyProps={{
          component: 'span'
        }} />
          </Grid>
          <Grid md={7.5} xs={12}>
            <Stack spacing={1} sx={{
          p: 3,
          borderRadius: 2,
          bgcolor: 'background.neutral'
        }}>
              {section.items.map(item => <FormControlLabel key={item.name} control={<Switch checked={formik.values[item.name]} onChange={formik.handleChange} />} disabled={formik.isSubmitting} label={<Stack alignItems="center" direction="row" spacing={1}>
                      <Typography variant="body2">{item.label}</Typography>
                    </Stack>} name={item.name} sx={{
            m: 0,
            width: 1,
            justifyContent: 'space-between',
            display: 'flex',
            flexDirection: 'row-reverse'
          }} />)}
            </Stack>
          </Grid>
        </Grid>)}

      <LoadingButton loading={updateUIEnhancementSettings.isPending} onClick={() => formik.handleSubmit()} sx={{
      ml: 'auto'
    }} variant="contained" data-sentry-element="LoadingButton" data-sentry-source-file="UIEnhancementsForm.tsx">
        Save Changes
      </LoadingButton>
    </CardContent>;
};
export default UIEnhancementsForm;